.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .image-wrapper {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .image {
    
    width: 100%;
    height: 300px;
    object-fit: contain;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  }
  
  .header {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
  }
  
  .intro {
    font-size: 16px;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .spacer {
    margin-bottom: 16px;
  }
  
  .step {
    font-size: 16px;
    font-weight: 500;
    margin-top: 10px;
    text-align: left;
    width: 100%;
    max-width: 600px;
  }
  
  .instruction {
    font-size: 14px;
    color: #555;
    text-align: left;
    width: 100%;
    max-width: 600px;
  }
  
  .example {
    background: #f5f5f5;
    padding: 8px 12px;
    border-radius: 6px;
    font-family: monospace;
  }
  
  .note {
    font-style: italic;
    margin-top: 10px;
  }
  
  .detail {
    margin-left: 20px;
    font-size: 14px;
    display: block;
  }
  
  .response {
    font-size: 14px;
    color: #007bff;
  }
  
  .footer {
    font-size: 16px;
    font-weight: bold;
    margin-top: 30px;
    text-align: center;
  }
  